/* Estructura principal */

.tarea-formulario{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

/* Campo de texto */

.tarea-input{
  width: 350px;
  font-size: 18px;
  background-color: #fff;
  padding: 14px 32px 14px 16px;
  border-radius: 4px 0 0 4px;
  border: 2px solid #00471b;
  outline: none;
}

/* Boton agregar tarea */

.tarea-boton{
  padding: 16px;
  font-size: 18px;
  font-family: lato, sans-serif;
  border: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  outline: none;
  background: #00471b;
  color: #fff;
  text-transform: capitalize;
}